html {
  overflow-y: scroll;
}

@media (min-width: 320px) {
  /* smartphones, portrait iPhone, portrait 480x320 phones (Android) */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: 100%;
  }
  .folder-container {
    display: grid;
    grid-template-columns: auto auto;
  }

  .dashboardSpecial {
    padding-right: 10px;
    padding-left: 10px;
  }
  .title-document {
    font-size: 30px;
  }
  .content-container {
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 480px) {
  /* smartphones, Android phones, landscape iPhone */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: 100%;
  }
  .folder-container {
    display: grid;
