    margin-right: auto;
  }
}
@media (min-width: 600px) {
  /* portrait tablets, portrait iPad, e-readers (Nook/Kindle), landscape 800x480 phones (Android) */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: 100%;
  }
  .widget-folder {
    display: grid;
    grid-template-columns: auto auto auto;
  }

  .dashboardSpecial {
    padding-right: 30px;
    padding-left: 30px;
  }
  .title-document {
    font-size: 50px;
  }
  .content-container {
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 801px) {
  /* tablet, landscape iPad, lo-res laptops ands desktops */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: auto auto;
  }
  .folder-container {
    display: grid;
    grid-template-columns: auto auto auto;
  }

  .dashboardSpecial {
    padding-right: 30px;
    padding-left: 30px;
  }
  .title-document {
    font-size: 50px;
  }
  .content-container {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1025px) {
  /* big landscape tablets, laptops, and desktops */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: auto auto;
  }
  .folder-container {
    display: grid;
    grid-template-columns: auto auto auto;
  }

  .dashboardSpecial {
    padding-right: 30px;
    padding-left: 30px;
  }
  .title-document {
    font-size: 65px;
  }
  .content-container {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1150px) {
  /* big landscape tablets, laptops, and desktops */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: auto auto auto;
  }
  .folder-container {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
  }

  .dashboardSpecial {
    padding-right: 50px;
    padding-left: 50px;
  }
  .documentWrapper {
    margin: auto;
  }
  .title-document {
    font-size: 65px;
  }
  .content-container {
    margin-left: 28vw;
  }
}
@media (min-width: 1281px) {
  /* hi-res laptops and desktops */
  body {
    /* margin-bottom: 200px !important; */
    padding-top: 80px;
  }
  .widget-container {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 0px;
  }
  .folder-container {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    padding: 0px;
  }

  .dashboardSpecial {
    padding-right: 100px;
    padding-left: 100px;
  }
  .title-document {
    font-size: 65px;
  }
  .content-container {
    margin-left: 400px;
  }
}
.button-table-of-content.item-table-of-content{margin-top:7px;cursor:pointer}.item-table-of-content span.activated{color:#2196f3}

.button-table-of-content{text-align:left;display:block;overflow-x:hidden;border-radius:.5rem .5rem .5rem .5rem;background-color:#fff;color:#565656;padding:10px 10px 8px;font-size:14px;width:250px}.button-table-of-content.parent-table-of-content{background-color:#2196f3;overflow-x:visible;color:#fff;margin-top:20px}.button-table-of-content .text-table-of-content{line-height:12.5pt;letter-spacing:.025em}

.sidebar{-webkit-transition:.5s ease-in-out;background-color:#e4e4e4;border-radius:1rem 1rem 1rem 1rem;height:80vh;margin-bottom:1000px;margin-right:10px;max-width:80vw;overflow-x:hidden;overflow-y:auto;padding:40px 18px 10px;transition:0.5s ease-in-out;width:300px;z-index:2}.sidebar.sticky-sidebar{position:fixed;top:100px}.sidebar.scroll-sidebar{position:fixed;top:450px}.sidebar.l-toggle-on{left:2vw}.sidebar.l-toggle-off{left:-300px}.sidebar .sidebar-content-container{margin-bottom:1000px}.openbtn{-webkit-transition:.5s ease-in-out;background-color:#2ca1fd;border-radius:50% 50% 50% 50%;color:#fff;cursor:pointer;display:flex;font-size:20px;font-weight:bolder;height:40px;justify-content:center;align-items:center;margin-left:calc((2vw + 300px)/2);transition:0.5s ease-in-out;width:40px;z-index:2}.openbtn.sticky-openbtn{position:fixed;top:80px}.openbtn.scroll-openbtn{position:fixed;top:430px}.openbtn.ml-toggle-on{margin-left:calc(140px + 1vw)}.openbtn.ml-toggle-off{margin-left:2vw}.openbtn span{margin:0}.button-shadow{background-color:#fff;border-radius:2rem 2rem 2rem 2rem;color:#565656;display:inline-block;font-size:14px;padding:8px 24px;text-align:center}.sidebar,.button-shadow,.button-table-of-content{box-shadow:0 0 6px rgba(157,157,157,0.33);border:1px solid #d8d8d8}.flex-mb-500{display:flex;flex-direction:column;align-items:center;margin-bottom:500px}

.hero-title-container{display:block;min-height:500px;position:relative;background-color:#0575e6;background-image:linear-gradient(rgba(255,255,255,0.08), rgba(255,255,255,0.08), rgba(255,255,255,0.08), rgba(255,255,255,0.73), #fff),linear-gradient(0deg, #00f260, #0575e6)}.hero-title-container .dashboard-special{padding-right:100px;padding-left:100px}.hero-title-container .dashboard-special .title-document{font-weight:bolder;color:#000;line-height:110%;position:absolute;top:70%;transform:translateY(-50%);font-size:65px}

.author-profile-image{background-color:rgba(0,0,0,0);background-color:#dcdcdc;height:80px;width:80px;border-radius:50%;display:inline-block;vertical-align:middle;border:2px solid #b0b0b0;box-shadow:0 0 12px #0000004e;-webkit-box-shadow:0 0 12px #0000004e;-moz-box-shadow:0 0 12px #0000004e}.date{font-size:12px;color:black}.profile-detail{display:inline-block;vertical-align:middle;margin-left:10px;font-size:17px;color:black}.mt-30{margin-top:30px}

.mb-2oo{margin-bottom:200px}.content-container{width:720px;max-width:80vw;margin-left:400px;margin-right:auto}

*,::after,::before{box-sizing:border-box}body{-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;color:#212529;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-size:1rem;font-weight:400;line-height:1.5;margin:0;padding-top:0;text-align:left}a{text-decoration:none}a:hover{text-decoration:underline}

